.comments-page {
  max-width: 600px;
  margin: 40px auto;
  padding: 20px;
  background: #f4f4f4;
  border-radius: 12px;
  font-family: Arial, sans-serif;
}

.comment-form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.comment-form input,
.comment-form select,
.comment-form textarea {
  padding: 10px;
  border-radius: 6px;
  border: 1px solid #ccc;
  font-size: 16px;
}

.comment-form button {
  background-color: #3498db;
  color: white;
  border: none;
  padding: 12px;
  border-radius: 6px;
  cursor: pointer;
  font-weight: bold;
  transition: background 0.3s;
}

.comment-form button:hover {
  background-color: #2980b9;
}

.comments-list {
  margin-top: 30px;
}

.comment-box {
  background: white;
  padding: 15px;
  border-radius: 8px;
  margin-bottom: 15px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
}

.comment-box h4 {
  margin: 0 0 5px;
}

.comment-box .rating {
  font-weight: bold;
  margin: 5px 0;
  color: #f39c12;
}

.comment-box .date {
  font-size: 12px;
  color: #888;
}
