/* Footer Styles */
.footer {
  background-color: #2c3e50;
  color: #ecf0f1;
  padding: 40px 0 20px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.footer-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
}

.footer-logo {
  grid-column: 1 / -1;
  margin-bottom: 20px;
}

.footer-title {
  font-size: 28px;
  color: #3498db;
  margin-bottom: 10px;
  font-weight: bold;
}

.footer-description {
  color: #bdc3c7;
  line-height: 1.6;
  max-width: 400px;
}

.footer-section {
  margin-bottom: 30px;
}

.footer-section-title {
  font-size: 18px;
  margin-bottom: 20px;
  color: #3498db;
  position: relative;
  padding-bottom: 10px;
  font-weight: 600;
}

.footer-section-title::after {
  content: '';
  position: center;
  left: 0;
  bottom: 0;
  width: 99px;
  height: 2px;
  background-color: #e74c3c;
}

.footer-links {
  list-style: none;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.footer-link {
  color: #bdc3c7;
  text-decoration: none;
  transition: color 0.3s;
}

.footer-link:hover {
  color: #3498db;
}

.footer-column-group {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
}

.footer-bottom {
  text-align: center;
  padding-top: 30px;
  margin-top: 30px;
  border-top: 1px solid #34495e;
}

.social-icons {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-bottom: 20px;
}

.social-icon {
  display: inline-flex;
  width: 40px;
  height: 40px;
  background-color: #34495e;
  color: #ecf0f1;
  border-radius: 50%;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  transition: all 0.3s;
}

.social-icon:hover {
  background-color: #3498db;
  transform: translateY(-3px);
}

.footer-bottom-links {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  margin-bottom: 15px;
}

.footer-bottom-link {
  color: #bdc3c7;
  text-decoration: none;
  transition: color 0.3s;
}

.footer-bottom-link:hover {
  color: #3498db;
}

.copyright {
  color: #7f8c8d;
  font-size: 14px;
}

/* Responsive Styles */
@media (max-width: 768px) {
  .footer-container {
    grid-template-columns: 1fr;
  }

  .footer-column-group {
    grid-template-columns: 1fr;
  }
}