* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Segoe UI', sans-serif;
}

body {
  background: #f8f9fa;
  color: #333;
}

header {
  background: #6c5ce7;
  color: white;
  padding: 2rem;
  text-align: center;
}

.section {
  padding: 2rem;
}

h2 {
  margin-bottom: 1rem;
  color: #2d3436;
}

.card-container {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  justify-content: space-between;
}

.card {
  background: white;
  padding: 1rem;
  border-radius: 8px;
  display: flex;
  align-items: center;
  gap: 1rem;
  flex: 1 1 calc(48% - 1rem);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  min-width: 300px;
   height: 200px;
}

.card img {
  width: 200px;
  height: 150px;
  object-fit: cover;
  border-radius: 8px;
}

.card-text {
  flex: 1;
}

.card h3 {
  color: #0984e3;
  margin-bottom: 0.5rem;
}

footer {
  text-align: center;
  padding: 1rem;
  background: #dfe6e9;
  margin-top: 2rem;
}