body {
  background-color: #ececec;
  color: #000000;
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}

h1 {
  text-align: center;
  padding: 20px 0;
  font-size: 2.5rem;
  font-weight: 600;
  color: #000000;
  margin: 20px 0;
}

h2 {
  font-size: 1.5rem;
  font-weight: 630;
  color: #000000;
  margin: 0 20px 15px;
  text-align: left;
}

.boxes {
  display: flex;
  flex-direction: column;
  gap: 30px;
  padding: 20px;
}

.box {
  width: 100%;
  text-align: left;
  padding: 10px;
}

.box h2 {
  padding-left: 10px;
}

.banner-container {
  display: flex;
  flex-direction: row;
  overflow-x: scroll;
  overflow-y: auto;
  gap: 30px;
  padding: 10px;
  scroll-behavior: smooth;
  scrollbar-width: thin;
  scrollbar-color: #888 #f4f4f4;
}

.banner-container::-webkit-scrollbar {
  height: 8px;
}

.banner-container::-webkit-scrollbar-thumb {
  background-color: #dfdfdf;
  border-radius: 4px;
}

.banner-container::-webkit-scrollbar-track {
  background: #f4f4f4;
}

.banner {
  flex: 0 0 auto;
  width: 280px;
  background-color: #f4f4f4;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
  position: relative;
}

.banner:hover {
  transform: translateY(-5px);
}

.banner a {
  text-decoration: none;
  color: #000000;
  display: block;
}

.place-image {
  width: 100%;
  height: 150px;
  object-fit: cover;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
}

.bookmark-icon-btn {
  background-color: #d3d3d3;
  color: #000000;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  padding: 6px 12px;
  margin: 8px auto; /* Centered horizontally */
  border-radius: 6px;
  width: fit-content;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.bookmark-icon-btn:hover {
  background-color: #b0b0b0;
}

.bookmark-icon-btn svg {
  width: 18px;
  height: 18px;
}

.banner h3 {
  font-size: 1.2rem;
  margin: 10px;
  font-weight: 600;
}

.banner p {
  font-size: 0.9rem;
  margin: 0 10px 10px;
  color: #333333;
}

.banner p strong {
  color: #000000;
}

.btn-group {
  display: flex;
  justify-content: center;
  gap: 10px;
  padding: 10px;
  background-color: #f4f4f4;
}

.btn-group button:first-child {
  background-color: #ffd700;
  color: #000000;
}

.btn-group button:first-child:hover {
  background-color: #e6c200;
}

.hero-image-section {
  position: relative;
  width: 100%;
  height: 50vh;
  background-image: url('https://images.pexels.com/photos/1323550/pexels-photo-1323550.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.overlay-text h1 {
  font-size: 12rem;
  color: #ffffff;
  margin-bottom: 1rem;
  position: center;
}

@media (max-width: 768px) {
  .banner {
    width: 200px;
  }

  h1 {
    font-size: 2rem;
  }

  h2 {
    font-size: 1.3rem;
  }

  .boxes {
    padding: 10px;
    gap: 20px;
  }
}
