.bookmark-page {
  padding: 30px;
  max-width: 1000px;
  margin: auto;
  background-color: #fdfdfd;
}

.bookmark-page h1 {
  font-size: 32px;
  margin-bottom: 25px;
  text-align: center;
  color: #333;
}

.bookmark-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 20px;
}

.bookmark-card {
  background-color: #fff;
  border-radius: 12px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.07);
  padding: 20px;
  transition: transform 0.2s ease;
}

.bookmark-card:hover {
  transform: translateY(-5px);
}

.bookmark-card h2 {
  font-size: 22px;
  margin-bottom: 10px;
  color: #007bff;
}

.bookmark-card p {
  font-size: 14px;
  color: #555;
  margin: 5px 0;
}

.timestamp {
  font-size: 12px;
  color: #999;
}

.map-link {
  display: inline-block;
  margin-top: 10px;
  padding: 8px 12px;
  background-color: #28a745;
  color: white;
  border-radius: 6px;
  text-decoration: none;
  font-size: 14px;
  transition: background-color 0.3s ease;
}

.map-link:hover {
  background-color: #218838;
}

.delete-btn {
  background-color: #dc3545;
  color: white;
  border: none;
  padding: 6px 12px;
  border-radius: 4px;
  margin-top: 10px;
  font-size: 14px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.delete-btn:hover {
  background-color: #c82333;
}
