/* CoochBehar.css */
body {
  background: #f8f9fa;
  color: #333;
  line-height: 1.6;
}

.cooch-header {
  background: #7a7887;
  color: white;
  padding: 2rem;
  text-align: center;
}

.cooch-header h1 {
  margin-bottom: 0.5rem;
  font-size: 2.5rem;
}

.cooch-header p {
  font-size: 1.2rem;
}

.section {
  padding: 2rem;
}

.section h2 {
  margin-bottom: 1rem;
  color: #2d3436;
  font-size: 1.8rem;
}

.card-container {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  justify-content: space-between;
}

.card {
  background: white;
  padding: 1rem;
  border-radius: 8px;
  display: flex;
  align-items: center;
  gap: 1rem;
  flex: 1 1 calc(48% - 1rem);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  min-width: 300px;
  height: 200px;
  transition: transform 0.2s ease;
}

.card:hover {
  transform: scale(1.02);
}

.card img {
  width: 200px;
  height: 150px;
  object-fit: cover;
  border-radius: 8px;
}

.card-text {
  flex: 1;
}

.card h3 {
  color: #0984e3;
  margin-bottom: 0.5rem;
  font-size: 1.2rem;
}

.view-link {
  display: inline-block;
  margin-top: 4px;
  color: #0984e3;
  text-decoration: none;
  font-weight: 500;
}

.view-link:hover {
  text-decoration: underline;
}

.cooch-footer {
  text-align: center;
  padding: 1rem;
  background: #dfe6e9;
  margin-top: 2rem;
  font-size: 0.9rem;
  color: #636e72;
}

@media (max-width: 768px) {
  .card {
    flex: 1 1 100%;
    flex-direction: column;
    height: auto;
    text-align: center;
  }

  .card img {
    width: 100%;
    height: auto;
  }
}
