* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

body {
  background-color: #dbdbdb;
}

/* HERO section */
.hero {
  text-align: center;
  margin-bottom: 2rem;
}

.hero img {
  max-width: 100%;
  height: auto;
  border-radius: 12px;
}

/* Recommendation Section */
.recommendations {
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), 
              url("https://images.pexels.com/photos/31817439/pexels-photo-31817439/free-photo-of-ancient-temples-in-pattadakal-karnataka.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding: 30px 20px;
  margin: 20px;
  border-radius: 10px;
  position: relative;
  color: white;
}

.recommendations h1 {
  font-size: 42px;
  margin-bottom: 20px;
}

.recommendations p {
  font-size: 18px;
  margin-bottom: 20px;
  max-width: 80%;
  color: #ffffff;
}

.start-btn {
  background-color: #FFD700;
  border: none;
  color: #111;
  padding: 15px 40px;
  border-radius: 30px;
  font-size: 20px;
  font-weight: bold;
  cursor: pointer;
  position: absolute;
  right: 30px;
  bottom: 30px;
  transition: transform 0.2s;
}

.start-btn:hover {
  transform: scale(1.05);
}

/* Smol floaty cards */
.floaty-container {
  overflow: hidden;
  width: 100%;
  margin-top: 2rem;
}

/* ✅ Scroll effect for banners */
.floaty-track {
  display: flex;
  width: max-content;
  animation: scroll 60s linear infinite;
}

/* ✅ Pause scroll on hover */
.floaty-container:hover .floaty-track {
  animation-play-state: paused;
}

@keyframes scroll {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(-50%);
  }
}

.floaty.smol.guys {
  flex: 0 0 auto;
  width: 600px;
  height: 600px;
  margin-right: 1rem;
  text-align: center;
  background-color: #f0f0f0;
  border-radius: 12px;
  padding: 1rem;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s;
}

.floaty.smol.guys:hover {
  transform: scale(1.05);
}

.floaty.smol.guys img {
  width: 100%;
  height: 80%;
  object-fit: cover;
  border-radius: 8px;
  margin-bottom: 0.5rem;
}

.floaty-container a {
  text-decoration: none;
  color: inherit;
}

.floaty-container a h2,
.floaty-container a p {
  margin: 0;
}
