/* Recommend.css */
.recommend-page {
  max-width: 900px;
  margin: 0 auto;
  padding: 20px;
}

.recommend-page h1 {
  text-align: center;
  margin-bottom: 25px;
}

.recommend-form {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
  margin-bottom: 30px;
}

.recommend-form label {
  font-weight: bold;
  margin-right: 10px;
}

.recommend-form select {
  padding: 8px 12px;
  font-size: 16px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.recommend-form button {
  padding: 10px 16px;
  background-color: #ffd700; /* yellow */
  color: black;
  border: none;
  border-radius: 6px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s;
}

.recommend-form button:hover {
  background-color: #e6c200; /* darker yellow */
}

.recommend-results {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.recommend-card {
  background-color: #f8f8f8;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}

.recommend-card h3 {
  margin-top: 0;
  color: #333;
}

.recommend-card a {
  color: #007bff;
  text-decoration: none;
  font-weight: bold;
}

.recommend-card a:hover {
  text-decoration: underline;
}
